<template>
  <div>
    <van-nav-bar title="注册" left-arrow @click-left="$router.go(-1)" />
    <p class="tips">请确保你的邮箱正确，用于接收验证码短信</p>

    <van-form>
      <van-field
        v-model="em"
        name="邮箱"
        label="邮箱"
        placeholder="邮箱"
        :error-message="type?'邮箱格式错误':''"
        :rules="[{ required: true, message:res }]"
      />

      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />

      <van-field v-model="sms" center clearable label="邮箱验证码" placeholder="请输入邮箱验证码">
        <template #button>
          <van-button v-show="!send" size="small" type="primary" @click="btn">发送验证码</van-button>
          <van-button v-show="send" size="small" type="primary" color="#ccc">{{ time+"秒后重发" }}</van-button>
        </template>
      </van-field>

      <div style="margin: 16px;">
        <van-button
          ref="btn"
          round
          block
          type="info"
          color="#ff464e"
          native-type="submit"
          @click="onSubmit"
        >下一步</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      em: "",
      password: "",
      testing: "",
      bool: false,
      res: "请填写邮箱",
      sms: "",
      type: false,
      send: false,
      num: null,
      time: 60,
      setTime: null
    };
  },
  methods: {
    btn() {
      if (this.password == "" || this.em == "") {
        return;
      }
      this.$axios
        .post("http://127.0.0.1:3000/getcode", { emi: this.em })
        .then(res => {
          // console.log(res.data);
          if (res.data.err == -1) {
            this.type = true;
          } else {
            this.type = false;
            this.send = true;
            Toast("发送成功");
            this.setTime = setInterval(() => {
              this.time--;
              if (this.time <= 0) {
                clearInterval(this.setTime);
                this.send = false;
              }
            }, 1000);
          }
        });
    },
    onSubmit() {
      // console.log(123)
      // console.log(this.sms);
      this.$axios.post("http://127.0.0.1:3000/register", { emi: this.em ,code:this.sms ,password:this.password
      })
      .then(res=>{
        // console.log(res);
        if (res.data.err==0) {
            this.$router.push("/login")
        }else{
            Toast("请输入正确的信息")
        }
      })
    }
  },
  created() {
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
  },
  watch: {}
};
</script>

<style lang="scss" scoped>
.van-nav-bar__title {
  font-size: 18px;
}
.van-nav-bar .van-icon {
  font-size: 22px;
  color: #000;
}
.tips {
  color: #999;
  font-size: 12px;
  margin-top: 28px;
  margin-bottom: 20px;
  line-height: 15px;
  text-align: center;
}
</style>